@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;1,300&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}
*::-webkit-scrollbar {
    width: 8px;
    background-color: grey;
  }


a{
    text-decoration: inherit;
    color: inherit;
}

.app{
    min-height: 98.5vh;
    border: 8px solid #39445a93;
    margin:5px;
    padding: 5px;
}
.footer{
    text-align: center;
    margin-bottom: 10px;
    font-size: 18px;
    font-family: "Montserrat",sans-serif;
}

.link{
    font-weight: 600;
}
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
   
}

.header__title{
    text-transform: uppercase;
    font-size: 5vw;
    font-family: "Montserrat",sans-serif;
    cursor:pointer;
}

.header__divider{
    margin: 10px;
    width: 100%;
    background-color: grey;
}
.error{
    width:"100%";
    padding:10px;
    margin-bottom: 10px;
    border-radius: 4px;
    text-align: center;
    color: red;
    text-transform:capitalize;
}
.home__content{
    display: flex;
    justify-content: space-around;
    
}

.home__settings{
    width:45%;
    padding:10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Poppins",sans-serif;
    font-weight: 300;
}


.home__heading{
    font-size: 30px;
}

.home__banner{
    width:55%;
    align-self: center;
    padding: 8px;
}

.home__settingselect{
    display: flex;
    flex-direction: column;
    padding: 20px;
    width:100%;
    flex:0.8 1;
    
}

.textfield{
    margin-bottom: 30px !important;
}

@media (max-width: 800px)
{
    .home__content{
        flex-direction: column-reverse;
        align-items: center;
    }

    .home__banner{
        width:85%;
        padding: 0;
        padding-top: 20px;
    }
    .home__settings{
        width: 100%;
    }
}
.question{
    width:100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.singleques{
    width:95%;
    min-height: 350px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    align-items: center;
    border:5px solid grey;
    padding: 15px;
    margin-top: 10px;
}

.options{
    width:100%;
    display: flex;
    flex-wrap: wrap;
    flex:1 1;
    align-items: center;
    justify-content: space-around;
    margin:10px;
    
}

.singleoption{
    width:46%;
    height:50px;
    padding:13px 20px;
    margin:10px;
    box-shadow: 0 0 2px black;
    font-size: 18px;
    border-radius: 10px;
}

@media (max-width: 614px)
{
    .singleoption{
        width: 100%;
    }
}

.select{
    background-color: rgb(7,207,0);
    color:white;
    box-shadow: 0 0 1px black;
}

.wrong{
    background-color: rgb(233,0,0);
    color:white;
    box-shadow: 0 0 1px black;
}

.controls{
    display: flex;
    width: 100%;
    justify-content: space-around;
}
.quiz{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: "Poppins",sans-serif;
}

.quiz__subtitle{
    font-size:25px;
    border:1px solid black;
    box-shadow: 4px 4px 2px black;
    padding:5px 10px;
}
.quiz__info{
    font-weight: 700;
    font-size: 24px;
    width:95%;
    display: flex;
    justify-content: space-between;
    text-transform: uppercase;
    margin: 30px 0px;
}

 
.loader{
    margin: 100px;
}

.result{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height:60vh;

    
}
.title{
    font-size:40px;
    padding:5px 10px;
    font-family: "Poppins",sans-serif;
}

.button{
    height:60px !important;
    
}
*{
    margin:0;
}
